//https://stackoverflow.com/questions/44480644/string-union-to-string-array
// create type from array
type NormalIcons = (typeof IconFontLigatures)[number]
type BoldIcons = `${NormalIcons}_b`
type LightIcons = `${NormalIcons}_l`
export type Icons = NormalIcons | BoldIcons | LightIcons

// create icon tokens (list of ligatures)
const CreateIconTokens = (): string[] => {
    const result: string[] = []
    IconFontLigatures.forEach((e) => {
        result.push(e)
        result.push(`${e}_l`)
        result.push(`${e}_b`)
    })
    return result
}
export const IconTokens = CreateIconTokens()
