import { LitElement, css, html } from 'lit'
import { property } from 'lit/decorators.js'

import type { Icons } from './iconFont'
import { computeStyle } from '../util/computeStyle'

export default class RuiIcon extends LitElement {

    @property() name: Icons = 'home'
    @property() size: 's' | 'm' | 'l' | 'inherit' | number | string = 'inherit'
    @property() color: string = 'inherit'

    render() {
        return html`
            <div class='container' style="
                color: ${computeStyle("color", this.color)};
                font-size: ${computeStyle("size", this.size)};
            ">
                ${this.name}
            </div>
        `
    }

    static styles = css`
        .container {
            display: flex;
            align-items: center;
            height: 100%;
            font-family: 'RICOH-Icon-Font';
            line-height: 100%;
            user-select: none;
        }
    `
}




