export function computeStyle(key, value) {
    // カスタムプロパティが定義されている場合、そのプロパティを使用する
    if (getComputedStyle(document.documentElement).getPropertyValue(`--rui-${key}-${value}`)) {
        return `var(--rui-${key}-${value})`;
    }
    // カスタムプロパティが定義されていない場合、colorをそのまま使用する
    else {
        return value;
    }
}